import './App.css';
import Die from './Die';
import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import {nanoid} from 'nanoid';
import Confetti from 'react-confetti';
import {AiFillPlusSquare} from "../node_modules/react-icons/ai";
import {AiFillMinusSquare} from "../node_modules/react-icons/ai";
import Timer from "./Timer";

function App() {
  const [num,setnum]=React.useState(10)
  const [die,setDie]=React.useState(die_random(num))
  const [tenzies,setTenzies]=React.useState(false)
  const [time, setTime] = React.useState(0);
  const [start, setStart] = React.useState(false);
  const [end, setIsEnd] = React.useState(tenzies);

  React.useEffect(() => {
    const allHeld = die.every(die=>die.isHeld)
    const firstValue = die[0].value
    const allSame = die.every(die => die.value === firstValue)
    if(allHeld && allSame){
      setTenzies(true)
      setStart(false)
    }
  },[die])

  React.useEffect(() => {
    setDie(die_random(num))
  },[num])

  function Roll(){
    setDie(old=>old.map(die => {
      return die.isHeld ? die : {...die,value: Math.floor(Math.random() * 6)+1} 
    }))
    if(start == false){
      setStart(true)
    }
  }

  function holdDice(id){
    setDie(old=>old.map(die => {
      return die.id === id ? {...die,isHeld: !die.isHeld} : die
    }))
    setStart(true)
  }
  function die_random(){
    let roll=[]
    for(let i=0;i<num;i++){
      roll.push({
        value: Math.floor(Math.random() * 6)+1,
        isHeld: false,
        id: nanoid()
      })
    }
    return roll;
  }

  function newGame(){
    setTenzies(false)
    setDie(die_random())
    setTime(0)
  }

  function Inc(){
    if(num>=20) console.log('Not allowed')
    else{
      setnum(old => old + 1)
      if(tenzies){
        setTenzies(false)
      }
      setStart(false)
      setTime(0)
    }
  }

  function Sub(){
    if(num<=5) console.log('Not allowed')
    else setnum(old => old - 1)
    if(tenzies){
      setTenzies(false)
    }
    setStart(false)
    setTime(0)
  }
  React.useEffect(() => {
    let interval = null;
    
    if (start) {
      interval = setInterval(() => {
        setTime((time) => time + 10);
      }, 10);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [start]); 

  const diceElements=die.map(die=>(<Die key={die.id} value={die.value} isHeld={die.isHeld} Hold={()=>holdDice(die.id)}/>))

  return (
    <div className="container-fluid">
      {tenzies && <Confetti />}
      <div className='border' id='b1'>
        <div className='d-flex flex-column border m-4' id='b2'>
          <div className='col m-4' id='b3'>
            <h1>Tenzies</h1>
            <p>Roll until all dice are the same .Click each dice to freeze it at its current value</p>
          </div>
          <div className='d-block mx-auto'>
              <button className='fa-lg mx-5 px-1 border-0' onClick={Inc} id='buton'><AiFillPlusSquare size={28}/></button>
              <button className='mx-5 px-1 border-0' onClick={Sub} id='buton'><AiFillMinusSquare size={28}/></button>
          </div>
          <div className="stop-watch">
            <Timer time={time} />
          </div>
          <div className='row row-cols-5 p-2 m-auto justify-content-center' id='b4'>
            {diceElements}
          </div>
          <div className='col-auto d-block my-3 mx-auto'>
            {tenzies ? <button onClick={newGame} id='button'>New Game</button> : <button onClick={Roll} id='button'>Roll</button>}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;